<template>
  <div class="yt-card" :class="{'is-edge': edge, 'is-shadow': shadow}">
    <div class="yt-card-header" :class="{'is-tag': tag}" v-if="title">
      <span class="yt-card-title">{{title}}</span>
      <span class="yt-card-more">
        <!-- @slot  header -->
        <slot name="more"></slot>
      </span>
    </div>
    <!-- @slot  header -->
    <slot name="header"></slot>
    <div class="yt-card-body">
      <!-- @slot content-->
      <slot></slot>
      <div class="yt-card-btn" v-if="btn">
        <yt-btn @click="handler" size="small" plain round>{{btn}}</yt-btn>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'yt-card',
    props: {
      /**
       * card title
       */
      title: {
        type: String
      },
      /**
       * btn
       */
      btn: {
        type: String
      },
      /**
       * 
       */
      shadow: {
        type: Boolean,
        default: true
      },
      /**
       * title
       */
      tag: {
        type: Boolean,
        default: true
      },
      /**
       * 
       */
      edge: {
        type: Boolean,
        default: true
      }
    },
    methods: {
      handler(e) {
        /**
         * @event btn-click
         * @description 
         * @type {event}
         */
        this.$emit('btn-click', e)
      }
    }
  }
</script>



