<template>
  <div class="yt-form">
    <div class="yt-form-wrapper">
      <!--@slot -->
      <slot></slot>
    </div>
    <!--@slot btn-->
    <slot v-if="$slots.btn" name="btn"></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'yt-form',
    provide() {
      return {
        ytForm: this
      }
    },
    props: {
      /**
       * 
       */
      full: {
        type: Boolean,
        default: true
      },
      /**
       * form   btn input textarea
       */
      disabled: {
        type: Boolean,
        default: false
      },
      /**
       *  
       */
      model: {
        type: Object,
        default() {
          return {}
        }
      },
      /**
       * 
       */
      rules: {
        type: Object,
        default() {
          return {}
        }
      }
    }
  }
</script>



