<template>
  <div class="yt-radioList" @change="$emit('change', currentValue)">
    <!-- @slot -->
    <slot name="title">
      <div class="yt-radioList-title" v-if="title">{{title}}</div>
    </slot>
    <div class="yt-radioList-wrapper">
      <yt-radio v-model="currentValue" v-for="(option, index) in list"
                :disabled="option.disabled" :name="option | securityGetVal(val) || option"
                :pos="pos" :inline="inline" :key="index">
        <!--@slot label-->
        <slot v-bind="option">{{option | securityGetVal(label)}}</slot>
      </yt-radio>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'yt-radioList',
    props: {
      /**
       *  radioList 
       */
      title: String,
      /**
       *  radio    'left' || 'right'
       */
      pos: {
        type: String,
        default: 'left'
      },
      /**
       *  radio inline
       */
      inline: {
        type: Boolean,
        default: true
      },
      /**
       *  inputvalue   item  item[val] 
       */
      val: {
        type: String,
        default: 'value'
      },
      /**
       *    item  item[label] 
       */
      label: {
        type: String,
        default: 'label'
      },
      /**
       *  
       */
      list: {
        type: Array,
        required: true
      },
      /**
       *  
       */
      value: {}
    },
    computed: {
      currentValue: {
        get() {
          return this.value
        },
        set(val) {
          this.$emit('input', val)
        }
      }
    }
  }
</script>



