<template>
  <div class="yt-page" :style="pageStyle">
    <!-- @slot page  header-->
    <slot name="header">
      <yt-header @change="change" v-bind="$attrs" v-model="key">
        <!-- @slot header -->
        <slot name="title" v-if="$slots.title"></slot>
        <!-- @slot header -->
        <slot name="right" v-if="$slots.right" slot="right"></slot>
        <!-- @slot header -->
        <slot name="left" v-if="$slots.left" slot="left"></slot>
      </yt-header>
    </slot>

    <div class="yt-page-container" :class="{ 'is-scroll': scroll, 'is-flex': flex }">
      <!-- @slot -->
      <slot></slot>
    </div>

    <!-- @slot  -->
    <slot name="alert"></slot>

    <transition name="yt-fade">
      <div class="yt-page-mask" @click="_more = false" v-if="_more"></div>
    </transition>

    <transition name="yt-page-more">
      <div class="yt-page-more" v-if="_more">
        <!-- @slot more  more-->
        <slot name="more"></slot>
      </div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'yt-page',
    props: {
      /**
       * 
       */
      fillColor: {
        type: String,
        default: '#f3f4f6'
      },
      /**
       *  flex column
       */
      flex: {
        type: Boolean,
        default: false
      },
      /**
       *  
       */
      scroll: {
        type: Boolean,
        default: false
      },
      /**
       *     slot=more
       */
      more: {
        type: Boolean,
        default: false
      },
      /**
       * @model
       * @description 
       */
      value: {
        type: [String, Number],
        default: ''
      }
    },
    computed: {
      pageStyle() {
        return {
          backgroundColor: this.fillColor
        }
      },
      key: {
        get() {
          return this.value
        },
        set(val) {
          /**
           * 
           *
           * @event input
           * @type {string} 
           */
          this.$emit('input', val)
        }
      },
      _more: {
        get() {
          return this.more
        },
        set(val) {
          /**
           * more
           *
           * @event update:more
           * @type {string} 
           */
          this.$emit('update:more', val)
        }
      }
    },
    methods: {
      change(key) {
        /**
         *  
         *
         * @event change
         * @type {string} 
         */
        this.$emit('change', key)
      }
    }
  }
</script>



