<template>
  <label class="yt-radio" :class="inline ? 'is-inline' : 'is-block'">
    <span class="yt-radio-label" v-if="pos === 'right'">
      <slot>{{label}}</slot>
    </span>
    <span class="yt-radio-wrapper" :class="`is-${pos}`">
      <input class="yt-radio-input" type="radio" v-model="currentValue" :disabled="_disabled" :value="name">
      <span class="yt-radio-core"></span>
    </span>
    <span class="yt-radio-label" v-if="pos === 'left'">
      <!--@slot label-->
      <slot>{{label}}</slot>
    </span>
  </label>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'yt-radio',
    inject: {
      ytForm: {
        default: () => ({})
      }
    },
    props: {
      /**
       *    left || right
       */
      pos: {
        type: String,
        default: 'right'
      },
      /**
       * inline
       */
      inline: {
        type: Boolean,
        default: true
      },
      /**
       * 
       */
      disabled: {
        type: Boolean,
        default: false
      },
      /**
       * checkboxvalue
       */
      name: {
        type: [String, Number, Object],
        required: true
      },
      /**
       * @model
       * @description checkbox
       */
      value: {},
      /**
       * radio
       */
      label: {
        type: String
      }
    },
    computed: {
      _disabled() {
        return this.disabled || (this.ytForm || {}).disabled
      },
      currentValue: {
        get() {
          return this.value
        },
        set(value) {
          this.$emit('input', value)
        }
      }
    }
  }
</script>



